import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';
import { environment } from '../../environments/environment';

@Injectable({ providedIn: 'root' })
export class AuthenticationService {

    mytoken: Object;
    constructor(private http: HttpClient) { }

    login(email: string, password: string) {
        const myheader = new HttpHeaders().set('Authorisation','Token '+localStorage.getItem("token"));
        console.log(myheader);
        return this.http.post<any>('/api/login', { email, password }, {headers:myheader})
            
            
    }

    
    register( name: string, email: string, password: string) {
        const myheader = new HttpHeaders().set('Authorisation','Token '+localStorage.getItem("token"));
        console.log(myheader);
        return this.http.post<any>('/api/save', { name, email, password }, {headers:myheader})
        
    }

    logout() {
        // remove user from local storage to log user out
        localStorage.removeItem('currentUser');
    }


    generateToken(){
        
        const myheader = new HttpHeaders().set('Content-Type','application/x-www-form-urlencoded');
        return this.http.get('/generate');
    }

}