import { Component, PipeTransform, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { DecimalPipe } from '@angular/common';
import { FormControl, FormBuilder, FormGroup, Validators } from '@angular/forms';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import { Observable, from } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { UserService , AuthenticationService} from '../services';
import { NotesService } from '../services/notes.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css'],
  providers: [DecimalPipe]
})
export class HomeComponent implements OnInit {
  uid: number;
  name: string;
  auth: Object;
  notes: Object;
  filter = new FormControl('');
  closeResult: string;
  addNoteForm: FormGroup;
  loading = false;
  submitted = false;
  error = '';
  display='none';
  constructor(
    private formBuilder: FormBuilder,
    private modalService: NgbModal, 
    private userService: UserService, 
    private authenticationService: AuthenticationService, 
    private notesService: NotesService,
    private route: ActivatedRoute,
    private router: Router
    ) { }

  ngOnInit() {

    this.addNoteForm = this.formBuilder.group({
      text: ['', Validators.required]
  });

    this.uid = parseInt(sessionStorage.getItem("id"));
    this.name = sessionStorage.getItem("name");
    

  this.authenticationService.generateToken().subscribe((auth) => {
    this.auth = auth
  });

  this.notesService.getAllNotes(this.uid).subscribe(data => {this.notes = data;console.log(data);});

  }

  openToDoModal(){
    this.display='block';
  }

  open(content) {
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }

  get f() { return this.addNoteForm.controls; }

  onSubmit() {
    this.submitted = true;
    if (this.addNoteForm.invalid) {
        return;
    }
    this.loading = true;
    console.log("I am submitted");
    this.notesService.addNote(this.f.text.value, this.uid).subscribe(data => {
      console.log(data);
      if(data.status == 'error'){
        this.error = data.message;
        this.loading = false;
      }
      if(data.status == 'success'){
        this.loading = false;
        this.notesService.getAllNotes(this.uid).subscribe(data => {this.notes = data;console.log(data);});
        //location.reload();
      }
      
    },
    error => {
        this.error = error;
        this.loading = false;
    });
  }

  deleteNote(id:number){

    if(!confirm("Are you sure?")){
      return false;
    }

    this.notesService.deleteANote(id, this.uid).subscribe(data => {
      if(data.status == 'error'){
        this.error = data.message;
        this.loading = false;
      }
      if(data.status == 'success'){
        this.loading = false;
        this.notesService.getAllNotes(this.uid).subscribe(data => {this.notes = data; });
      }
    });

  }

  changeNoteStatus(id:number,st:number){
    let newSt = (st == 1)?0:1;
    if(!confirm("Are you sure?")){
      return false;
    }
    
    this.notesService.changeNoteStatus(this.uid, id, newSt).subscribe(data => {
      this.notesService.getAllNotes(this.uid).subscribe(data => {this.notes = data; });
    });

  }

  redirectUrl(returnUrl: string){
    this.router.navigate([returnUrl]);
  }

}
