import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
@Injectable({
  providedIn: 'root'
})
export class NotesService {

  constructor(private http: HttpClient) { }

  
  getAllNotes(uid: number){
    return this.http.get("/notes/get-notes/"+uid);
  }

  addNote( text: string, uid: number) {
   
    return this.http.post<any>('/notes/add-note', { text, uid})   
  }

  deleteANote(id: number, uid: number){
    return this.http.delete<any>('/notes/delete/'+uid+'/'+id);
  }

  getANote(uid: number, id: number){
    return this.http.get("/notes/get-a-note/"+uid+"/"+id);
  }

  changeNoteStatus(uid: number, id: number, st: number){
    return this.http.put("/notes/is-completed/"+uid+"/"+id+"/"+st,{uid, id, st});
  }

}
